import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { OtherPagesPage } from './other-pages.page';

const routes: Routes = [
  {
    path: '',
    component: OtherPagesPage
  },
  {
    path: 'branch-locator',
    loadChildren: () => import('./branch-locator/branch-locator.module').then( m => m.BranchLocatorPageModule)
  },
  {
    path: 'special-offers',
    loadChildren: () => import('./special-offers/special-offers.module').then( m => m.SpecialOffersPageModule)
  },
  {
    path: 'help-and-support',
    loadChildren: () => import('./help-and-support/help-and-support.module').then( m => m.HelpAndSupportPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class OtherPagesPageRoutingModule {}
